/**
 * 
 * @param {Function} fun 
 * @param {Number} delay 
 */
export default function(fun, delay) {
	let timer = null
	let debounced = function() {
		let ctx = this
		let args = arguments
		if (timer) clearTimeout(timer)
		timer = setTimeout(function() {
			fun.apply(ctx, args)
		}, delay)
	}
	return debounced
}
