<template>
  <div>
    <script
      ref="script"
      :name="name"
      type="text/plain"
    ></script>
  </div>
</template>

<script>
  import LoadEvent from './event.js';
  import emitter from 'element-ui/src/mixins/emitter';
  import Debounce from './debounce.js';
  export default {
    name: 'richText',
    mixins: [emitter],
    data() {
      return {
        status: 0,
        initValue: '',
        mode: window.MutationObserver ? 'observer' : 'listener',
        defaultConfig: {
          UEDITOR_HOME_URL: '/ueditor/'
        },
        commonToolbars: [
          [
            "fullscreen",
            "source",
            "|",
            "undo",
            "redo",
            "|",
            "bold",
            "italic",
            "underline",
            "fontborder",
            "strikethrough",
            "superscript",
            "subscript",
            "removeformat",
            "formatmatch",
            "autotypeset",
            "blockquote",
            "pasteplain",
            "|",
            "forecolor",
            "backcolor",
            "insertorderedlist",
            "insertunorderedlist",
            "|",
            "rowspacingtop",
            "rowspacingbottom",
            "lineheight",
            "|",
            "customstyle",
            "paragraph",
            "fontfamily",
            "fontsize",
            "|",
            "directionalityltr",
            "directionalityrtl",
            "indent",
            "|",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyjustify",
            "|",
            "touppercase",
            "tolowercase",
            "|",
            "link",
            "unlink",
            "anchor",
            "|",
            "imagenone",
            "imageleft",
            "imageright",
            "imagecenter",
            "|",
            "emotion",
            "pagebreak",
            "|",
            "horizontal",
            "date",
            "time",
            "spechars",
            "|",
            "inserttable",
            "deletetable",
            "insertparagraphbeforetable",
            "insertrow",
            "deleterow",
            "insertcol",
            "deletecol",
            "mergecells",
            "mergeright",
            "mergedown",
            "splittocells",
            "splittorows",
            "splittocols",
            "charts",
            "|",
            "searchreplace",
            "drafts"
          ]
        ]
      };
    },
    computed: {
      mixedConfig() {
        let config = { toolbars: [...this.commonToolbars], ...this.config }
        return Object.assign({}, this.defaultConfig, config);
      }
    },
    props: {
      value: {
        type: String,
        default: ''
      },

      config: {
        type: Object,
        default: function () {
          return {};
        }
      },
      init: {
        type: Function,
        default: function () {
          return () => { };
        }
      },
      destroy: {
        type: Boolean,
        default: false
      },
      addImg: {
        type: Boolean,
        default: true
      },
      name: {
        type: String,
        default: ''
      },
      observerDebounceTime: {
        type: Number,
        default: 50,
        validator: function (value) {
          return value >= 20;
        }
      },
      observerOptions: {
        type: Object,
        default: function () {
          // https://developer.mozilla.org/en-US/docs/Web/API/MutationObserverInit
          return {
            attributes: true, //  DOM 
            attributeFilter: ['src', 'style', 'type', 'name'], // 
            characterData: true, // 
            childList: true, // 
            subtree: true // 
          };
        }
      },
      //  Vue  Nuxt  Vue SSR 
      forceInit: {
        type: Boolean,
        default: false
      },
      //  UEditor ID
      editorId: {
        type: String
      }
    },
    // v-model
    watch: {
      value: {
        handler(value) {
          // 
          if (value === null) {
            value = '';
          }
          // 0:  1: ready 2 ready
          switch (this.status) {
            case 0:
              this.status = 1;
              this.initValue = value;
              //  process.client  Nuxt 
              (this.forceInit || (typeof process !== 'undefined' && process.client) || typeof window !== 'undefined') && this._checkDependencies().then(() => {
                this.mode = window.MutationObserver ? 'observer' : 'listener';
                this.$refs.script ? this._initEditor() : this.$nextTick(() => this._initEditor());
              });
              break;
            case 1:
              this.initValue = value;
              break;
            case 2:
              this._setContent(value);
              break;
            default:
              break;
          }
        },
        immediate: true
      }
    },
    deactivated() {
      this.editor && this.editor.removeListener('contentChange', this.contentChangeHandler);
      this.observer && this.observer.disconnect();
    },
    beforeDestroy() {
      if (this.destroy && this.editor && this.editor.destroy) {
        this.editor.destroy();
      }
      if (this.observer && this.observer.disconnect) {
        this.observer.disconnect();
      }
    },
    methods: {
      // 
      _initEditor() {
        this.$refs.script.id = this.id = this.editorId || 'editor_' + Math.random().toString(16).slice(-6); //  Vue SSR id  data  id  BUG
        this.init();
        this.addImg && this.addImgEvent()
        this.$emit('before-init', this.id, this.mixedConfig);
        this.$emit('beforeInit', this.id, this.mixedConfig); //  DOM  BUG https://vuejs.org/v2/guide/components-custom-events.html#Event-Names
        this.editor = window.UE.getEditor(this.id, this.mixedConfig);
        this.editor.addListener('ready', () => {
          if (this.status === 2) { //  keep-alive 
            this.editor.setContent(this.value);
          } else {
            this.status = 2;
            this.$emit('ready', this.editor);
            if (this.initValue) {
              this.editor.setContent(this.initValue);
            }
          }
          if (this.mode === 'observer' && window.MutationObserver) {
            this._observerChangeListener();
          } else {
            this._normalChangeListener();
          }

        });
      },
      addImgEvent() {
        let that = this;
        UE.registerUI("addImg", function (editor, uiName) {
          editor.registerCommand(uiName, {
            execCommand: function () {
              alert('execCommand:' + uiName)
            }
          });
          //button
          var btn = new UE.ui.Button({
            //
            name: 'addImg',
            //
            title: '',
            //iconicon
            cssRules: 'background-position: -380px 0',
            //
            onclick: function () {
              that.$pcTpl.imgChoose.popup({
                picMax: 1000
              }).then((img) => {
                if (img.length > 0) {
                  let imgELE = img.reduce((total, current) => {
                    let image_path = current.image_path
                    if (image_path) {
                      return total + `<img src="${image_path}"/>`
                    } else {
                      return total
                    }
                  }, '')
                  editor.execCommand('insertHtml', imgELE);
                }
              })
            }
          });
          return btn;
        });
      },
      // , UEditor 
      _checkDependencies() {
        return new Promise((resolve, reject) => {
          // ueditor.config.jsueditor.all.js(ueditor.config.jsUEUEDITOR_CONFIG,ueditor.all.jsUEDITOR_CONFIG,)
          let scriptsLoaded = !!window.UE && !!window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0 && !!window.UE.getEditor;
          if (scriptsLoaded) {
            resolve();
          } else if (window['$loadEnv']) { // script
            window['$loadEnv'].on('scriptsLoaded', () => {
              resolve();
            });
          } else {
            window['$loadEnv'] = new LoadEvent();
            // ueditor.all.min.jsueditor.config.jsueditor.all.min.jsueditor.config.js
            this._loadConfig().then(() => this._loadCore()).then(() => {
              resolve();
              window['$loadEnv'].emit('scriptsLoaded');
            });
          }
        });
      },
      _loadConfig() {
        return new Promise((resolve, reject) => {
          if (window.UE && window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0) {
            resolve();
            return;
          }
          let configScript = document.createElement('script');
          configScript.type = 'text/javascript';
          configScript.src = this.mixedConfig.UEDITOR_HOME_URL + 'ueditor.config.js';
          document.getElementsByTagName('head')[0].appendChild(configScript);
          configScript.onload = function () {
            if (window.UE && window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0) {
              resolve();
            } else {
              console.error('ueditor.config.js,UEDITOR_HOME_URL!\n', configScript.src);
            }
          };
        });
      },
      _loadCore() {
        return new Promise((resolve, reject) => {
          if (window.UE && window.UE.getEditor) {
            resolve();
            return;
          }
          let coreScript = document.createElement('script');
          coreScript.type = 'text/javascript';
          coreScript.src = this.mixedConfig.UEDITOR_HOME_URL + 'ueditor.all.js';
          document.getElementsByTagName('head')[0].appendChild(coreScript);
          coreScript.onload = function () {
            if (window.UE && window.UE.getEditor) {
              resolve();
            } else {
              console.error('ueditor.all.js,UEDITOR_HOME_URL!\n', coreScript.src);
            }
          };
        });
      },
      // 
      _setContent(value) {
        value === this.editor.getContent() || this.editor.setContent(value);
      },
      contentChangeHandler() {
        let val = this.editor.getContent();
        this.$emit('input', val);
        this.dispatch('ElFormItem', 'el.form.change', [val]);
      },
      //  UEditor  contentChange 
      _normalChangeListener() {
        this.editor.addListener('contentChange', this.contentChangeHandler);
      },
      //  MutationObserver API
      _observerChangeListener() {
        const changeHandle = (mutationsList) => {
          if (this.editor.document.getElementById('baidu_pastebin')) {
            return;
          }
          let val = this.editor.getContent();
          this.$emit('input', val);
          this.dispatch('ElFormItem', 'el.form.change', [val]);

        };
        // 
        this.observer = new MutationObserver(Debounce(changeHandle, this.observerDebounceTime));
        this.observer.observe(this.editor.body, this.observerOptions);
      }
    }
  };
</script>