<template>
  <div class="u-goods-edit">
    <good-item
      :list="goodsList"
      :config="config"
      :listType="config.listType"
    >
    </good-item>
    <el-card
      :header="isEdit ? '':''"
      class="edit-area"
      v-show="belongIndex === currentIndex"
    >
      <el-form
        style="text-align:left;"
        ref="ruleForm"
        :model="config"
        :rules="rules"
        label-width="120px"
      >
        <edit-panel :config="config">
          <el-form-item
            label=""
            prop="title"
          >
            <el-input
              size="mini"
              v-model="config.title"
              maxlength
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio
              v-model="config.titleFontsize"
              label="18px"
            ></el-radio>
            <el-radio
              v-model="config.titleFontsize"
              label="16px"
            ></el-radio>
            <el-radio
              v-model="config.titleFontsize"
              label="14px"
            ></el-radio>
          </el-form-item>
          <el-form-item label="">
            <el-color-picker v-model="config.titleBackgroundColor"></el-color-picker>
          </el-form-item>
          <el-form-item label="">
            <el-color-picker v-model="config.titleColor"></el-color-picker>
          </el-form-item>
          <el-form-item label="">
            <el-radio
              v-model="config.titleAlign"
              label="left"
            ></el-radio>
            <el-radio
              v-model="config.titleAlign"
              label="center"
            ></el-radio>
            <el-radio
              v-model="config.titleAlign"
              label="right"
            ></el-radio>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group
              v-model="config.auth_type"
              @change="handleAuthTypeChange"
            >
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item :label="config.rank_name">
            <el-select
              v-model="config.rank_id"
              placeholder=""
            >
              <el-option
                v-for="item in config.ranks"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="config.is_rank_only">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
        </edit-panel>
        <el-form-item label="">
          <rich-text v-model="config.decorateHtml"></rich-text>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
  import GoodItem from "../../good-style/Item.vue";
  import ConfigData from "./config";
  // import GoodsService from "@/api/goods/goods";
  import EditPanel from '../../good-style/component/edit-panel.vue';
  import goodsStyleConfig from '../../good-style/config'
  const originImgUrl = 'http://img.qianshetuan.cn/image/65/2020/12/31/d577c439e4abf20e9638b82bab50f30e.png';
  const originGoodsList = [
    {
      item_id: 1,
      thumb_image_path: originImgUrl,
      current_price: "0.00",
      selling_point: "",
      item_title: ""
    },
    {
      item_id: 2,
      thumb_image_path: originImgUrl,
      current_price: "0.00",
      selling_point: "",
      item_title: ""
    },
    {
      item_id: 3,
      thumb_image_path: originImgUrl,
      current_price: "0.00",
      selling_point: "",
      item_title: ""
    },
    {
      item_id: 4,
      thumb_image_path: originImgUrl,
      current_price: "0.00",
      selling_point: "",
      item_title: ""
    }
  ];
  export default {
    name: "GoodsGroupAdd",
    data() {
      return {
        goodsList: originGoodsList.slice(),
        config: {},
        configs: ConfigData,
        ranks: [],
        rules: {
          title: [
            { required: true, message: "", trigger: "blur" }
          ]
        }
      };
    },
    props: ["belongIndex", "currentIndex", "isGroup", "content", "isEdit"],
    components: {
      "good-item": GoodItem,
      'edit-panel': EditPanel,

    },
    methods: {
      handleAuthTypeChange(e) {
        this.config.rank_id = 0;
      }
    },
    created() {
      // todo 
      this.config = Object.assign(
        {
          title: "",  //
          auth_type: 1,
          titleFontsize: '18px',
          titleBackgroundColor: '#FFFFFF',
          titleColor: '#000000',
          titleAlign: 'left',
          rank_id: 0,
          rank_name: "",
          is_rank_only: 0,
          content: [1, 3, 4], //
          decorateHtml: "",
          ranks: [],
          ...goodsStyleConfig
        },
        this.content
      );
      this.goodsList = originGoodsList.slice();
    },
    watch: {
      currentIndex: function (n) {
        if (n === -1) {
          // this.isAll,this.value
          this.$emit("update:content", this.config);
        }
      },
      "config.auth_type": function (n) {
        switch (n) {
          case 2:
            this.config.rank_name = "";
            break;
          case 3:
            this.config.rank_name = "";
            break;
          default:
            this.config.rank_name = "";
            break;
        }
        // GoodsService.groupRankList({ auth_type: n }).then(res => {
        //   if (res.data.code) {
        //     this.config.ranks = res.data.data;
        //   }
        // });
        this.$pcTpl.axios({
          url: '/Item/groupRankList',
          method: 'post',
          data: {
            auth_type: n
          }
        }).then(res => {
          this.config.ranks = res.data.data;
        })
      }
    }
  };
</script>
